html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
}

body {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#APP,
#page,
#content,
.ast-container,
#primary,
#main,
article,
.entry-content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 0;
  margin: 0;
  overflow: hidden;
}

#Dialog {
  position: absolute;
  left: var(--wp--custom--ast-default-block-left-padding);
  bottom: 50%;
  transform: translateY(50%);
  width: 80%;
  max-width: 500px;
  overflow: hidden;
}
#Dialog .Slide {
  position: absolute;
  bottom: 100%;
  box-sizing: border-box;
  transition-property: bottom;
  transition-duration: 0.5s;
}
#Dialog .Slide.Current {
  bottom: 0;
}
#Dialog .wp-block-button.common {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 50%;
  background-color: var(--ast-global-color-2);
}
#Dialog .wp-block-button.common .wp-block-button__link {
  border: none;
  background-color: transparent;
  text-indent: 2000px;
  width: 32px;
  height: 32px;
  padding: 0;
  filter: invert(1);
}
#Dialog .wp-block-button.common.home .wp-block-button__link {
  background-image: url("~assets/images/home-alt.svg");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 60%;
}
#Dialog .wp-block-button.common.root .wp-block-button__link {
  background-image: url("~assets/images/arrow-circle-up.svg");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 60%;
}